body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}

body {
  font-family: 'Poppins', sans-serif;
  color: black;
  background-color: #f5f5dc; /* Brown background */
}

/* Navigation Styles */
nav {
  background-color: #333;
  color:black;
  display: flex;
  justify-content: space-between;
  padding: 15px 20px;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 100;
}

nav h1 {
  font-size: 22px;
}

nav ul {
  list-style: none;
  display: flex;
  gap: 15px;
}

nav ul li a {
  color:black;
  text-decoration: none;
  font-weight: bold;
}

nav ul li a:hover {
  color: black; /* Light beige */
}

/* Front Page */
.front-page {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.front-page h1 {
  font-size: 48px;
  color: black;
  margin-bottom: 15px;
}

.front-page p {
  font-size: 20px;
  margin-bottom: 20px;
}

.front-page .button {
  padding: 12px 25px;
  background-color: #d3b38d;
  color: #333;
  text-decoration: none;
  font-weight: 600;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.front-page .button:hover {
  background-color: #a67854;
}

/* Page Sections */
.page {
  padding: 100px 20px 40px;
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
  border-bottom: 1px solid #d3b38d;
}

h2 {
  font-size: 24px;
  color: #d3b38d;
  margin-bottom: 10px;
}

p {
  margin-bottom: 15px;
  line-height: 1.6;
}

/* Skills */
.skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 8px;
}

.skills span {
  background: #d3b38d;
  color: #333;
  padding: 8px 12px;
  border-radius: 20px;
  font-size: 14px;
}

/* Projects */
.project {
  background: rgba(255, 255, 255, 0.1);
  padding: 15px;
  margin: 15px 0;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}